import { Component } from '@angular/core';
import {LoadingController, NavController, NavParams, PopoverController, ViewController} from 'ionic-angular';
import {TermsPage} from "./terms/terms";
import {ProfilePage} from "../profile/profile";
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";
import {FormBuilder, NgForm} from "@angular/forms";

/**
 * Generated class for the RegisterPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  loading: any;
  responseData: any;
  userData = {
    device_id: '5c55a68c7e73178d9f927b844e65b7f887e8bdd496ce5c1e0c91449fa9a12c2a',
    device_type: 'iOS'
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public popoverCtrl : PopoverController,
    public viewCtrl: ViewController,
    public authService: AuthServiceProvider,
    public loadingCtrl: LoadingController,
    public formBuilder: FormBuilder
  ) {}

  terms_popup(){
    const popover = this.popoverCtrl.create(TermsPage);
    popover.present();
  }



  signup(form: NgForm){
    //Api Connections
    this.showLoader();
    this.authService.signup({formData: form.value}, "sign_up").then((result) => {
      this.responseData = result;
      console.log(this.responseData);
      localStorage.setItem('userData', JSON.stringify(this.responseData));
      this.loading.dismiss();
    }, (err) => {
      //Connection failed message
      console.log(err);
      this.loading.dismiss();
    })
  }

  showLoader(){
    this.loading = this.loadingCtrl.create({
      content: 'Please Wait..'
    })
  }

  loadProfile(){
    this.navCtrl.push(ProfilePage);
  }
}



